import card from '@/components/card'
import {
	api,
	barId,
	tips_msg
} from '@/const'
import login from '@/components/login'
import {
	checkT
} from '@/utils/net'
import {
	debounce
} from '@/utils'


const debounceOnScroll = () => debounce(function(e) {
	this.cursor = e.target.scrollTop
	// this.cursor = 0;
})
export default {
	data() {
		return {
			t: 0,
			page: 0,
			data: [],
			isLoading: false,
			animation: {},
			visible: false,
			cursor: 0,
		}
	},

	components: {
		card,
		login
	},
	onShow() {
		this.init();
	},
	onShareAppMessage: function() {
		return {
			title: '豆子社区', //分享内容
			path: '/pages/index/index' //分享地址
			// imageUrl: '/images/img_share.png',//分享图片
		}
	},
	methods: {
		onScroll: debounceOnScroll(),
		refresh() {
			this.init();
		},
		init() {
			var that = this;
			this.t = wx.getStorageSync("t");
			checkT(this.t,
				function() {
					wx.setStorageSync("t", 0);
					that.t = 0;
				},
				function() {
					that.t = wx.getStorageSync("t")
				}
			);
			this.getData(0)
			this.cursor = 0;
			this.page = 0;
		},
		closeModalEvent() {
			this.visible = false
			this.t = wx.getStorageSync('t')
			this.getData(0)
		},

		getData(page) {
			var type = -1
			uni.showLoading({
				title: '加载中'
			})
			this.isLoading = true
			var that = this;
			this.$http({
					act: 'post.list',
					barId: barId,
					pageNum: this.page,
					size: 5,
					t: this.t,
				},
				function(res) {
					if (res.data.list.length != 0) {
						if (that.data.length > 0 && page === 0) {
							// 下拉刷新
							that.data = res.data.list
							// res.data.data;
						} else {
							// 底部加载更多 h和初始得时候
							that.data = [...that.data, ...res.data.list]
						}
					} else {
						// 获取数据失败
						uni.showToast({
							title: '已经没有更新的数据了',
							icon: 'none',
							duration: 2000
						})
					}
					if (res.data.noticeSize > 0) {
						uni.setTabBarBadge({
							index: 2,
							text: res.data.noticeSize + ''
						})
					}

				}
			)
			uni.hideLoading()
			this.isLoading = false
		},
		async getMore() {
			if (!this.isLoading) {
				this.page += 1
				await this.getData(this.page)
			}
		},
		async onPullDownRefresh() {
			await this.getData(0)
			uni.stopPullDownRefresh()
		},
		changeTab(e) {
			const currentTab = e.target.dataset.tab
			const offset = e.target.dataset.offset
			this.tab = currentTab
			this.page = 0
			this.getData(currentTab, 0)
			this.animation = uni
				.createAnimation({
					duration: 1000,
					timingFunction: 'ease'
				})
				.left(`-${offset * 100}vw`)
				.step()
				.export()
		}
	}
}
